import React, { useState, useEffect } from "react";
import {Card,Button} from "antd";
import axios from "axios";
import './style.css';

const{Meta} = Card;

function MyApp() {
    const[news, setNews] = useState ([]);
   
    useEffect(() => {
        const loadNews = async () => {
        const response = await axios.get("https://archive.org/advancedsearch.php?q=subject:google+sheets&output=json")
        setNews(response.data.articles);
        }
       loadNews();
    }, []);

    console.log("news", news);
   
    return (
        <div className="MyApp">
<br/>
<h2 className="display-5">News feed</h2>
<br/>

{news && news.map((item, index) => {
    return(
        <Card className="card" key={index} hoverable
            style={{width: "70%"}}
            cover={<img alt="image" src={item.urlToImage}/>}>
        <Meta className="Meta" title= {item.title} description={item.content}/>
      <a href={item.url} target="_blank" rel="noopener noreferrer">  <button type="button" class="btn btn-primary"> Read more</button> 
      </a>
        </Card>
    )
})}
        </div>
    );
}

export default MyApp;