import React, {useState, useEffect} from "react";
import firebaseDb from "./firebase";
import {useHistory, useParams} from "react-router-dom";
import {isEmpty} from "lodash";


const AddEdit = () => {
    const values = {
        name: "",
        description: "",
        location: "",
        age: "",
    };

    const [data, setData] = useState({});
    const [initialState, setState] = useState(values);
    const {name, description, location, age} = initialState;
    
const history = useHistory();

let currentId = useParams();
const {id} = currentId;

useEffect(() => {
    firebaseDb.child("users").on("value", (snapshot) => {
     if (snapshot.val() !== null) {
         setData({
             ...snapshot.val(),
     });
     }   else    {
         snapshot({});
}

});
}, [id]);

useEffect(() => {
if(isEmpty(id)) {
    setState({...values});
}else{
    setState({...data[id]});
}
},[id,data ]);

    const handleInputChange = (e) => {
        let {name, value} = e.target;
        setState({
            ...initialState,
            [name]: value,
        });

    };

    const handleSubmit = (e) => {
        e.preventDefault();
        if(isEmpty(id)) {
            firebaseDb.child("users").push(initialState, (err) => {
                if(err) {
                    console.log(err);
                }
            });
        } else {
            firebaseDb.child(`/users/${id}`).set(initialState, (err) => {
                if(err) {
                    console.log(err);
                }
            });
        }

        history.push("/");

    };

    return (
        <div className="container mt-5">
            
            <div className="row">
                <div className="col-md-6">
                    <form onSubmit={handleSubmit}>
                        <h1 className="display-2">Edit user details</h1>
                        <br/>

                    <div className="form-group">
                        <label className="bmd-label-floating">
                            Name
                        </label>

                        <input type="text" className="form-control" value={name} name="name" onChange={handleInputChange}/>
                    </div>

                    <div className="form-group">
                        <label className="bmd-label-floating">
                            Description
                        </label>

                        <input type="text" className="form-control" value={description} name="description" onChange={handleInputChange}/>
                    </div>

                    <div className="form-group">
                        <label className="bmd-label-floating">
                            Location
                        </label>

                        <input type="text" className="form-control" value={location} name="location" onChange={handleInputChange}/>
                    </div>

                    <div className="form-group">
                        <label className="bmd-label-floating">
                            Age
                        </label>

                        <input type="text" className="form-control" value={age} name="age" onChange={handleInputChange}/>
                    </div>
                    <br/>
                    <button className="btn btn-default">Cancel</button>
                    <button className="btn btn-success btn-ras">Update</button>

                    </form>
                </div>

            </div>

        </div>
    )
}

export default AddEdit