import React, { useEffect, useState } from 'react'
import APIDATA from './apiData';
import { useParams } from 'react-router';

const Details = () =>{

    const [ info, setInfo ] = useState([]);

    const { id : id } = useParams();
    const { name : name } = useParams();
    const { brand : brand } = useParams();

   
    //get data by id77

    useEffect(() =>{
        fetch("http://makeup-api.herokuapp.com/api/v1/products.json?brand=maybelline" + id ) 
        .then((res) => { 
        return res.json()
    })
        .then((data) => {
             setInfo(data);
             });
    });
    return(
        <>
        {/*this is for displaying the data from the previous page*/}

        <div className="container-fluid mt-4">

        <div className="card">
  <div className="card-header">
    Featured id: {id} 
  </div>
  <div className="card-body">
    <h5 className="card-title"><p>Hello{info.id}</p>{info.name}</h5>
    <p className="card-text">{info.id}</p>

    {
      info.map((action) =>   
        <a href={"/details/" + action.id } className="list-group-item list-group-item-action active" key={action.id} aria-current="true">

        {action.name} </a>
          

      )
    }
    <a href="#" className="btn btn-primary">Go somewhere</a>
  </div>
</div>
</div>
        </>
    
    )
    
}

export default Details