import React, {useState, useEffect} from 'react'
import firebaseDb from "./firebase";
import {useHistory, useParams} from "react-router-dom";
import {isEmpty} from "lodash";
import {Link} from "react-router-dom";
import 'bootstrap/dist/css/bootstrap.min.css';
import './style.css';


const ListRecord = () => {
    
    const [data, setData] = useState({});


    useEffect(() => {
        firebaseDb.child("users").on("value", (snapshot) => {
         if (snapshot.val() !== null) {
             setData({
                 ...snapshot.val(),
         });
         }   else    {
             snapshot({});
    }

    });
    }, []);

    const onDelete = (id) =>{
        if(window.confirm('Are you sure you to delete this user?')){
            firebaseDb.child(`users/${id}`).remove((err) => {
                if(err) {
                    console.log(err);
                }
            });
        }
    };
    
    const values = {
        name: "",
        description: "",
        location: "",
        age: "",
    };

    const [initialState, setState] = useState(values);
    const {name, description, location, age} = initialState;
    
const history = useHistory();

let currentId = useParams();
const {id} = currentId;

useEffect(() => {
    firebaseDb.child("users").on("value", (snapshot) => {
     if (snapshot.val() !== null) {
         setData({
             ...snapshot.val(),
     });
     }   else    {
         snapshot({});
}

});
}, [id]);

useEffect(() => {
if(isEmpty(id)) {
    setState({...values});
}else{
    setState({...data[id]});
}
},[id,data ]);

    const handleInputChange = (e) => {
        let {name, value} = e.target;
        setState({
            ...initialState,
            [name]: value,
        });

    };

    const handleSubmit = (e) => {
        e.preventDefault();
        if(isEmpty(id)) {
            firebaseDb.child("users").push(initialState, (err) => {
                if(err) {
                    console.log(err);
                }
            });
        } else {
            firebaseDb.child(`/users/${id}`).set(initialState, (err) => {
                if(err) {
                    console.log(err);
                }
            });
        }

        history.push("/");

    };
    
return(
<div className="flexWrap">
<div className="w-75 p-3" id="first">
                <div className="col-md-6">
                    <form onSubmit={handleSubmit} className="formed">
                    <div class="jumbotron jumbotron-fluid">
  <div class="container"> 
   <h1 className="display-5" id="up">

Add user details</h1>

                    <div className="form-group">
                        <label className="bmd-label-floating">
                            Name
                        </label>

                        <input type="text" className="form-control" value={name} name="name" onChange={handleInputChange}/>
                    </div>

                    <div className="form-group">
                        <label className="bmd-label-floating">
                            Description
                        </label>

                        <textarea class="form-control" placeholder="Leave a description here" value={description} name="description" onChange={handleInputChange}></textarea>
                  
                    </div>

                    <div className="form-group">
                        <label className="bmd-label-floating">
                            Location
                        </label>

                        <input type="text" className="form-control" value={location} name="location" onChange={handleInputChange}/>
                    </div>

                    <div className="form-group">
                        <label className="bmd-label-floating">
                            Age
                        </label>

                        <input type="number" className="form-control" value={age} name="age" onChange={handleInputChange}/>
                    </div>
                    <br/>
                    <button className="btn btn-success btn-ras">Submit</button>
                    </div></div>

                    </form>
                </div>

            </div>

<br/>
<div className="w-75 p-3" id="second">
<div className="jumbotron jumbotron-fluid">

    <div className="col-lg-12">
    <div class="p-4 shadow-4 rounded-3">

<h1 className="display-5">User details</h1>

<br/>
        <table className="table table-bordered table-striped">
            <thead className="thead-dark">
            <tr>

                <th scope="col">Name</th>
                <th scope="col">Action</th>


            </tr>
            </thead>
            <tbody>
                {Object.keys(data).map((id, index) => {
                    return (
                       <tr key={id}>
                           <th>{data[id].name}</th>
                         
                            <td>
                                <Link to={`/update/${id}`}>
                                    <a className="btn text-primary">Update</a>
                                </Link>
                                    <a className="btn text-primary" onClick={() => onDelete(id)}>Delete</a>
                            
                            </td>
                       </tr> 
                    )

                    
                }
                
                )}
            </tbody>
        </table>
    </div>
</div>
</div>
</div></div>
    );
};

export default ListRecord;